.movie__card {
  display: flex;
  flex-direction: row;
  margin-top: 15px;
  margin-bottom: 15px;
}
.movie__poster > img {
  width: 50vw;
  min-width: 200px;
  height: auto;
  box-shadow: 0px 6px 3px -2px rgba(0, 0, 0, 0.36);
  border-radius: 8px;
}
.movie__paragraph {
  display: flex;
  flex-direction: column;
  margin-top: 24px;
  gap: 6px;
}
.movie__desc {
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  padding-left: 15px;
  gap: 20px;
}
.movie__genres {
  display: flex;
  flex-direction: row;
  gap: 10px;
}
.movie__score {
  color: darkgray;
}

.movie__homepage:hover,
.movie__homepage:focus {
  color: orange;
}
